import Link from "next/link";
import { notFound } from "next/navigation";
import {
  ArrowLeft,
  AlertCircle,
  Brain,
  MapPin,
  Sparkles,
  Trophy,
  Users,
} from "lucide-react";
import ProblemCapture from "@/components/ProblemCapture";
import { colleges } from "@/lib/data";

interface CollegePageProps {
  params: {
    id: string;
  };
}

const facilityCards = [
  {
    title: "Problem Capture Portal",
    description:
      "Collect verified incidents with timelines, attachments, and anonymous protection so decisions stay data-backed.",
    icon: AlertCircle,
    href: "#problems",
  },
  {
    title: "Wisdom Vault",
    description:
      "Retain senior-to-junior playbooks, course hacks, and mentorship notes that appear contextually when juniors need them.",
    icon: Brain,
    href: "#wisdom",
  },
  {
    title: "Predictive Warnings",
    description:
      "Forecast upcoming risks by comparing multi-year patterns for your campus with peer institutions across India.",
    icon: Sparkles,
    href: "#warnings",
  },
  {
    title: "Gamified Impact",
    description:
      "Celebrate contributors with badges, streaks, and leaderboards that keep the community engaged month after month.",
    icon: Trophy,
    href: "#gamification",
  },
];

export default function CollegePage({ params }: CollegePageProps) {
  const college = colleges.find((item) => item.id === params.id);

  if (!college) {
    notFound();
  }

  const isAmity = college.id === "amity-gwalior";
  const gwaliorPeers = colleges.filter(
    (item) =>
      item.city.toLowerCase() === "gwalior" && item.id !== college.id
  );

  const metrics = [
    {
      label: "Living Memory Index",
      value:
        college.memoryIndex > 0 ? `${college.memoryIndex}%` : "Coming Soon",
      helper:
        college.memoryIndex > 0
          ? "Strength of resolved issues and retained lessons"
          : "Publish your first campus insight to activate",
    },
    {
      label: "Active Contributors",
      value:
        college.activeUsers > 0
          ? college.activeUsers.toLocaleString()
          : "Awaiting Pioneers",
      helper:
        college.activeUsers > 0
          ? "Students keeping the memory vault alive"
          : "Invite the first 25 members to unlock leaderboards",
    },
    {
      label: "City",
      value: `${college.city}, ${college.state}`,
      helper: "Madhya Pradesh Knowledge Corridor",
    },
    {
      label: "Established",
      value: college.established,
      helper: "Legacy that fuels the learning graph",
    },
  ];

  return (
    <main className="min-h-screen bg-gradient-to-b from-marble-white via-white to-university-blue/5">
      <header className="relative overflow-hidden bg-gradient-to-r from-primary-dark via-primary to-university-blue text-white">
        <div className="absolute inset-0 opacity-20 bg-[radial-gradient(circle_at_top,_rgba(255,255,255,0.3),_transparent_60%)]" />
        <div className="relative z-10 mx-auto flex max-w-7xl flex-col gap-12 px-4 py-24 md:px-6 lg:px-8">
          <div className="flex flex-wrap items-center justify-between gap-4 text-sm text-white/80">
            <Link
              href="/"
              className="inline-flex items-center gap-2 rounded-full bg-white/10 px-4 py-2 font-semibold backdrop-blur transition hover:bg-white/20"
            >
              <ArrowLeft className="h-4 w-4" />
              Back to main campus
            </Link>
            <span className="inline-flex items-center gap-2 rounded-full bg-white/10 px-4 py-2">
              <MapPin className="h-4 w-4" />
              {college.city}, {college.state}
            </span>
          </div>

          <div className="grid gap-12 lg:grid-cols-[1.1fr,0.9fr]">
            <div className="space-y-6">
              <span className="inline-flex items-center gap-2 rounded-full bg-white/20 px-3 py-1 text-sm font-medium uppercase tracking-wide">
                {college.type} Institution
              </span>
              <h1 className="font-heading text-4xl font-bold leading-tight md:text-5xl lg:text-6xl">
                {college.name}
              </h1>
              <p className="max-w-2xl text-base text-white/80 md:text-lg">
                A dedicated Campus Memory space for {college.shortName}. Track
                lived campus experiences, mobilise student wisdom, and align
                administration decisions with real data from the ground.
              </p>

              <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-4">
                {metrics.map((metric) => (
                  <div
                    key={metric.label}
                    className="rounded-3xl border border-white/20 bg-white/10 p-4 text-white shadow-sm backdrop-blur"
                  >
                    <p className="text-xs uppercase tracking-wide text-white/60">
                      {metric.label}
                    </p>
                    <p className="mt-2 text-2xl font-semibold">{metric.value}</p>
                    <p className="mt-3 text-xs text-white/70">{metric.helper}</p>
                  </div>
                ))}
              </div>
            </div>

            <div className="relative">
              <div className="aspect-[4/3] w-full rounded-3xl border-2 border-dashed border-white/40 bg-white/10 p-6 text-center shadow-xl backdrop-blur">
                <div className="flex h-full flex-col items-center justify-center rounded-2xl bg-white/10 text-white/80">
                  <span className="text-4xl">ðŸ“·</span>
                  <p className="mt-4 text-sm font-semibold uppercase tracking-wide">
                    Campus Photo Placeholder
                  </p>
                  <p className="mt-2 max-w-[14rem] text-xs text-white/70">
                    Drop a hero shot of the main block or upload later from the
                    admin console.
                  </p>
                </div>
              </div>
              <div className="absolute -bottom-8 left-6 right-6 rounded-2xl bg-white/90 p-4 text-primary shadow-lg">
                <div className="flex items-center justify-between">
                  <div className="space-y-1 text-left">
                    <p className="text-xs font-semibold uppercase tracking-wide text-primary/60">
                      Personalized memory deck
                    </p>
                    <p className="text-sm font-heading text-primary-dark">
                      Your uploads appear here instantly once published.
                    </p>
                  </div>
                  <Users className="h-6 w-6 text-primary" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </header>

      {isAmity && (
        <section className="mx-auto mt-16 max-w-7xl px-4 md:px-6 lg:px-8">
          <div className="rounded-3xl border-2 border-dashed border-primary/30 bg-white p-8 shadow-xl">
            <div className="flex flex-col gap-6 md:flex-row md:items-center md:justify-between">
              <div>
                <h2 className="font-heading text-2xl font-semibold text-primary-dark">
                  Amity University Gwalior Founders Board
                </h2>
                <p className="mt-2 max-w-2xl text-sm text-gray-600">
                  This block is reserved for your official highlightsâ€”placements,
                  flagship societies, leadership quotes, or campus milestones.
                  Populate it any time from the admin dashboard.
                </p>
              </div>
              <button className="rounded-full border border-primary/30 px-5 py-2 text-sm font-semibold text-primary transition hover:bg-primary hover:text-white">
                Add Real Data
              </button>
            </div>
            <div className="mt-8 grid gap-4 md:grid-cols-3">
              {["Signature Achievements", "Key Contacts", "Upcoming Focus"].map(
                (slot) => (
                  <div
                    key={slot}
                    className="rounded-2xl border border-dashed border-primary/20 bg-primary/5 p-5 text-center text-sm text-primary/60"
                  >
                    {slot} â€¢ Pending Input
                  </div>
                )
              )}
            </div>
          </div>
        </section>
      )}

      <section className="mx-auto mt-20 max-w-7xl px-4 md:px-6 lg:px-8">
        <div className="flex flex-col gap-4 text-center">
          <span className="mx-auto inline-flex items-center gap-2 rounded-full bg-primary/10 px-4 py-1 text-sm font-semibold text-primary">
            Platform Facilities
          </span>
          <h2 className="font-heading text-3xl font-bold text-gray-900">
            Everything Campus Memory does for your college
          </h2>
          <p className="mx-auto max-w-3xl text-sm text-gray-600 md:text-base">
            Each block below maps directly to a working module you can launch
            today. Data you share through any module updates instantly for your
            college dashboard.
          </p>
        </div>
        <div className="mt-10 grid gap-6 md:grid-cols-2">
          {facilityCards.map((facility) => {
            const Icon = facility.icon;
            return (
              <a
                key={facility.title}
                href={facility.href}
                className="group flex h-full flex-col rounded-3xl border border-gray-100 bg-white p-6 shadow-sm transition hover:-translate-y-1 hover:border-primary/30 hover:shadow-lg"
              >
                <div className="flex items-center gap-4">
                  <span className="flex h-12 w-12 items-center justify-center rounded-2xl bg-primary/10 text-primary">
                    <Icon className="h-6 w-6" />
                  </span>
                  <h3 className="font-heading text-lg font-semibold text-gray-900">
                    {facility.title}
                  </h3>
                </div>
                <p className="mt-4 text-sm text-gray-600">
                  {facility.description}
                </p>
                <span className="mt-6 inline-flex items-center gap-2 text-sm font-semibold text-primary group-hover:gap-3">
                  Explore Module â†’
                </span>
              </a>
            );
          })}
        </div>
      </section>

      {gwaliorPeers.length > 0 && (
        <section className="mx-auto mt-20 max-w-7xl px-4 md:px-6 lg:px-8">
          <div className="rounded-3xl bg-white p-8 shadow-lg">
            <div className="flex flex-col gap-6 md:flex-row md:items-center md:justify-between">
              <div>
                <h2 className="font-heading text-2xl font-bold text-gray-900">
                  Gwalior City Memory Network
                </h2>
                <p className="mt-2 text-sm text-gray-600">
                  Switch across fellow Gwalior campuses to compare adoption and
                  share cross-campus learnings.
                </p>
              </div>
              <span className="inline-flex items-center gap-2 rounded-full bg-primary/10 px-4 py-2 text-sm font-semibold text-primary">
                <MapPin className="h-4 w-4" />
                {gwaliorPeers.length + 1} campuses connected
              </span>
            </div>
            <div className="mt-8 grid gap-4 sm:grid-cols-2">
              {gwaliorPeers.map((peer) => (
                <Link
                  key={peer.id}
                  href={`/colleges/${peer.id}`}
                  className="flex flex-col gap-2 rounded-2xl border border-gray-100 bg-gray-50/80 p-5 transition hover:-translate-y-1 hover:border-primary/40 hover:bg-white hover:shadow-md"
                >
                  <div className="flex items-center gap-3">
                    <span
                      className="h-3 w-3 rounded-full"
                      style={{ backgroundColor: peer.color }}
                    />
                    <p className="font-heading text-base font-semibold text-gray-900">
                      {peer.name}
                    </p>
                  </div>
                  <div className="flex items-center gap-4 text-xs text-gray-500">
                    <span>Memory Index: {peer.memoryIndex}%</span>
                    <span>Active users: {peer.activeUsers.toLocaleString()}</span>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        </section>
      )}

      <section className="mt-20">
        <ProblemCapture collegeId={college.id} />
      </section>

      <section
        id="warnings"
        className="mx-auto mt-6 max-w-7xl px-4 pb-16 pt-6 md:px-6 lg:px-8"
      >
        <div className="rounded-3xl border border-amber-100 bg-amber-50/80 p-6 shadow-sm">
          <h3 className="font-heading text-xl font-semibold text-amber-900">
            Predictive Warnings
          </h3>
          <p className="mt-2 text-sm text-amber-800/80">
            Alert models activate automatically once your campus has at least 12
            solved problems across two semesters. Until then, historical pattern
            training continues in the background.
          </p>
          <div className="mt-4 rounded-2xl border border-dashed border-amber-200 bg-white/80 p-4 text-sm text-amber-700">
            Minimum data required: 12 resolved reports Â· 3 prevention playbooks Â·
            1 verified seasonal trend.
          </div>
        </div>
      </section>

      <section
        id="wisdom"
        className="mx-auto mt-0 max-w-7xl px-4 pb-16 md:px-6 lg:px-8"
      >
        <div className="rounded-3xl border border-emerald-100 bg-emerald-50/80 p-6 shadow-sm">
          <h3 className="font-heading text-xl font-semibold text-emerald-900">
            Wisdom Vault
          </h3>
          <p className="mt-2 text-sm text-emerald-800/80">
            Unlock this space by inviting seniors to add survival tips, course
            crib sheets, or mentoring advice. Every submission stays tagged to
            batches, departments, and sentiment for quick discovery.
          </p>
          <div className="mt-4 grid gap-3 md:grid-cols-3">
            {["Course Hacks", "Mentor Directory", "Traditions & Culture"].map(
              (pill) => (
                <span
                  key={pill}
                  className="rounded-full border border-emerald-200 bg-white/80 px-4 py-2 text-center text-xs font-semibold text-emerald-700"
                >
                  {pill} â€¢ Add Soon
                </span>
              )
            )}
          </div>
        </div>
      </section>

      <section
        id="gamification"
        className="mx-auto mt-0 max-w-7xl px-4 pb-20 md:px-6 lg:px-8"
      >
        <div className="rounded-3xl border border-indigo-100 bg-indigo-50/80 p-6 shadow-sm">
          <h3 className="font-heading text-xl font-semibold text-indigo-900">
            Gamified Impact
          </h3>
          <p className="mt-2 text-sm text-indigo-800/80">
            Badges, streaks, and hall-of-fame rankings appear once your college
            onboards 50 verified contributors. Configure reward rules from the
            admin controls to match campus culture.
          </p>
          <div className="mt-4 flex flex-wrap gap-3">
            {["Problem Solver", "Memory Keeper", "Community Amplifier"].map(
              (badge) => (
                <span
                  key={badge}
                  className="rounded-full border border-indigo-200 bg-white/80 px-4 py-2 text-xs font-semibold text-indigo-700"
                >
                  {badge} â€¢ Locked
                </span>
              )
            )}
          </div>
        </div>
      </section>
    </main>
  );
}
